  -ms-user-select: none;
  user-select: none;
  -khtml-user-select: none;
}

.owl-carousel .owl-nav button.owl-prev, .owl-carousel .owl-nav button.owl-next, .owl-carousel button.owl-dot {
  color: inherit;
  font: inherit;
  background: none;
  border: none;
  padding: 0 !important;
}

.owl-carousel.owl-loaded {
